.favorites {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
}

.favorites h2 {
    font-size: 2em;
    color: #333;
    margin-bottom: 20px;
}

.favorites p {
    font-size: 1.2em;
    color: #666;
}

.favorite-card {
    background-color: #f8f8f8;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 15px;
    margin: 20px 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.favorite-card img {
    width: 100%;
    max-width: 200px;
    height: auto;
    border-radius: 8px;
    margin: 10px 0;
}

.favorite-card h3 {
    font-size: 1.5em;
    color: #444;
    margin: 10px 0;
}

.favorite-card p {
    font-size: 1em;
    color: #777;
    line-height: 1.5;
    margin: 10px 0;
}
