@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Merienda:wght@300..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Sevillana&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playwrite+CU:wght@100..400&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/*  title first */

.title-first {
    padding: 0.8rem 0;
    width: 100%;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    margin-top: 1rem;
    background-color: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(3px);
}

.title-main {
    font-weight: bold;
}

.title-sub {
    font-family: "Dancing Script", cursive;
    font-optical-sizing: auto;
    font-style: normal;
    font-size: 40px;
    color: #734060;
}

/* search bar */

.search {
    width: 100%;
    height: 3.3rem;
    margin-top: 18px;
    text-align: center;
    text-align: center;
    padding: 0 10%;

}

.search-bar {
    width: 100%;
    height: 3rem;
    background-color: rgb(255, 255, 255);
    border-radius: 20px 0px 0px 0px;
    box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.2);
    outline: none;
    border: none;
    padding: 0 30px;
    font-size: 15px;
    color: #734060;
    font-family: Verdana, Geneva, Tahoma, sans-serif;

}

.search-btn {
    width: 100%;
    height: 3.3rem;
    display: flex;
    border-radius: 20px 0 20px 0;
    align-items: center;
    text-align: center;

}

.search-btn:hover {
    border: 2px solid #734060;
}

.search button {
    background-color: rgb(255, 255, 255);
    border: none;
    outline: none;
    width: 4rem;
    height: 3rem;
    cursor: pointer;
    box-shadow: 5px 1px 5px rgba(0, 0, 0, 0.2);
    margin-left: -6px;
    border-radius: 0px 0px 20px 0px;
}

.search button i {
    font-size: 18px;
    color: #734060;

}



.section-background {
    width: 100%;
    height: 100%;
    object-fit: cover;
}


.slider {
    position: relative;
    width: 100%;
    max-width: 1150px;
    margin: 20px auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.slider-image {
    width: 100%;
    height: 350px;
    object-fit: cover;
    border-radius: 10px;
}

.slider-btn {
    background: rgba(0, 0, 0, 0.5);
    color: white;
    font-size: 24px;
    padding: 8px;
    border: none;
    cursor: pointer;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
    border-radius: 50%;
}

.prev-btn {
    left: 10px;
}

.next-btn {
    right: 10px;
}

.slider-btn:hover {
    background: rgba(0, 0, 0, 0.7);
}


/* Aboutme start */

.aboutme {
    display: flex;
    justify-content: center;
    align-items: center;
}

.aboutme-sub {
    width: 100%;
    height: 23rem;
    padding: 15px 10px 5px 10px;
    display: flex;
    background-color: #f9f6f6;
    margin-top: 0.6rem;
}

.aboutme-first {
    width: 60%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.hearts {
    font-size: 18px;

}

.first-text .first-sub span {
    font-family: "Playwrite CU", cursive;
    font-optical-sizing: auto;
    font-style: normal;
    font-size: 28px;
    color: #FF00ff;

}

.first-text .first-sub p {
    font-family: cursive;
    font-size: 20px;
}

#para {
    font-family: cursive;
    font-size: 20px;
}

#niceToMeet {
    font-family: "Cedarville Cursive", cursive;
    font-weight: 300;
    font-style: normal;
    font-size: 36px;
    color: #734060;

}

.first-text-item {
    width: 300px;
}

#learn-more {
    margin-top: 1rem;
    padding: 5px 10px;
    background-color: transparent;
    border: none;
    color: #734060;
    cursor: pointer;
    font-weight: bold;
    border-radius: 5px;
    background-color: #FFE8E8;
    font-size: 16px;
    font-family: "Merienda", cursive;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

#learn-more:hover {
    background-color: #FFFFFF;
    color: #ff00ff;
}

.aboutme-second {
    margin-top: -35px;
}

.aboutme-second img {
    width: 315px;
    object-fit: cover;
    border-radius: 20px 20px 20px 20px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
    background-size: cover;
}

/* button for title start */

.buttons-for-title {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: auto;
    padding: 0 20%;
    margin-top: 3rem;
}

.veg-btn,
.nonveg-btn,
.fast-btn {
    background-color: #734060;
    color: #FFFFFF;
    font-size: 18px;
    font-weight: bold;
    padding: 8px 24px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease, transform 0.2s ease;
}


/* title-card-start */

.title-cards-sub {
    width: 100%;
    padding: 0 60px;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}

.title-cards {
    width: 250px;
    margin: 30px 0px 5px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #FFE8E8;
    box-shadow: 0px 4px 15px rgba(255, 192, 203, 0.2);
    border-radius: 8px;
    padding: 1rem;
    transition: transform 0.3s ease, box-shadow 0.3s ease;

}

.title-cards:hover {
    transform: translateY(-5px);
    box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
}

.title-cards-one {
    width: 100%;
    height: 250px;
    justify-content: center;
    display: flex;
}

.title-cards-one img {
    width: 100%;
    height: 100%;
    border-radius: 8px;


}

.title-cards-btn {
    background-color: #fa766a;
    color: #FFFFFF;
    font-size: 16px;
    font-weight: bold;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
    /* box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1); */


}

.title-cards-two {
    font-size: 18px;
    font-weight: bold;
    color: #734060;

}

.title-cards-btn:hover {
    background-color: #e57373;

}

.recipe-details {
    color: #3d2b1f;
    background-color: #f9f5ef;
    padding: 20px 60px;
    border-radius: 5px;
    margin-top: 10px;

}
.recipe-details h1{
    margin: 10px 20px;
    color: #734060;
}
.recipe-details p{
    font-size: 19px;
    color:#000;
    margin: 20px;

}
.recipe-details img{
    width: 30rem;
    height: 30rem;
    margin: 20px;
    border-radius: 10px;
}
.recipe-details ul li {
    margin-top: 10px;
    font-size: 17px;
}
.recipe-details ul {
    margin-bottom: 20px;
}
.hide{
    margin: 10px;
    background-color: #fa746a;
    color: #FFFFFF;
    font-size: 16px;
    font-weight: bold;
    padding: 5px 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
}
.hide:hover{
    background-color: #3d2b1f;
}



@media(max-width:800px) {
    .title-first {
        flex-wrap: wrap;
    }

    .buttons-for-title {
        flex-wrap: wrap;
        padding: 0;
        margin-top: 30px;
    }
}

@media(max-width:500px) {
    .title-first {
        flex-wrap: wrap;
    }

    .title-sub {
        font-size: 37px;
    }

    .slider {
        margin-top: 8px;
    }

    section {
        height: 23rem;
    }

    .aboutme-sub {
        flex-wrap: wrap-reverse;
        height: 52rem;
        margin-top: -10px;

    }

    .aboutme-first {
        width: 100%;

    }

    .aboutme-second {

        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;


    }

    .aboutme-second img {
        width: 100%;
        margin: 0 10px;
        background-color: #e2f9f9;
        height: auto;


    }

    .slider-image {
        height: 250px;
    }

    .buttons-for-title {
        flex-wrap: wrap;
        padding: 0;
        margin: 0;
    }

    .title-cards-sub {
        padding: 0;
        margin: 0;
    }

    .title-cards {
        width: calc(49%);
        margin-top: 20px;

    }
    .recipe-details img{
    width: 100%;
    margin: 0;
    padding: 0;
    height: auto;
    }
}