@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Merienda:wght@300..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Sevillana&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playwrite+CU:wght@100..400&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.footer{
    background-color: #f7f4f4;
    color: #734060;
    padding: 40px 20px;
    margin-top: auto;
    text-align: center;
    
    
}
.footer-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    margin-top: auto;
    margin: auto;
    padding: 40px 40px;
    margin-top: auto;
    

}
.footer-about{
    width: 100%;
    color: white;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    height: 2rem;
}
.footer-about p{
    background-color: #734060;
    padding: 10px 100px;
    margin-top: auto;
}
.footer-logo-first{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.footer-logo-first div a{
    height: 100%;
    padding: 0 30px;
    text-decoration: none;
    display: flex;
    align-items: center;
    color:#734060;
    font-family: "Merienda",;
    

}
.footer-logo-first div li{
    list-style: none;
    margin-top: 15px;
}

.logo-footer p{
    color: #ff00ff;
    font-weight: bold;
    font-size: 32px;
    font-family: "Merienda", cursive;
}
.logo-and{
    color: #5D4037;
    font-size: 20px;
}
.footer-social .social-icons a {
    color: #734060;
    font-size: 20px;
    margin: 0 10px;
    text-decoration: none;
    transition: color 0.3s ease;
}
.footer-social{
    margin-top: 10px;
}

.footer-social .social-icons a:hover {
    color: #A04040;
}
.social-and-about{
    width: 40%;
 
}



.hearts{
    font-size: 18px;
    
}

.first-text .first-sub span {
    font-family: "Playwrite CU", cursive;
    font-optical-sizing: auto;
    font-style: normal;
    font-size: 28px;
    color: #FF00ff;
    
}
.first-text .first-sub p{
    font-family: cursive;
    font-size: 20px;
}
.first-sub span{
    font-size: 30px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
#para{
    font-family: cursive;
    font-size: 20px;
}
#niceToMeet{
    font-family: "Cedarville Cursive",cursive;
    font-weight: 300;
    font-style: normal;
    font-size: 36px;
    color: #734060;

}
.first-text-item{
    width: 300px;
    
    
}
.footer-aboutme{
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;


}
.footer-bottom {
    padding-top: 20px;
    font-size: 16px;
    border-top: 1px solid #e0dcdc;
    color: #734060;
    
}

.food-recipe ul li{
    font-size: 17px;
    list-style: none;
    color: #734060;
    margin-top: 10px;
}


.equal{
    
    width: auto;
    
    margin-top: auto;
}

@media(max-width: 500px){
    .footer-about p{
        padding: 0;
        
    }
    .food-recipe{
        display: none;
    }
    .footer-logo-first{
        margin-top: auto;
    }
    .social-and-about{
        margin-top: 15px;
    }
    .footer-container{
        align-items: center;
        justify-content: center;
    }
}