@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Merienda:wght@300..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Sevillana&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playwrite+CU:wght@100..400&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap');


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

header {
    height: 5rem;
    width: 100%;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
    background-color: #e0f0f0;
}
nav ul {
    width: 100%;
    height: 5rem;
    list-style: none;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

nav li {
    height: 50px;
    font-weight: 500;

}
nav a {
    height: 100%;
    padding: 0 30px;
    text-decoration: none;
    display: flex;
    align-items: center;
    color: #333333;
    font-family: "Merienda", cursive;
    font-weight: bold;

}
nav a:hover {
    background-color:#e2f9f9 ;
    color: #ff00ff;
    border-radius: 300px 10px 300px 10px;
}

nav li:first-child {
    margin-right: auto;
    font-size: 1.5rem;
    font-weight: bold;
}
nav li:first-child:hover a {
    background-color: transparent;
}
nav li:first-child a {
    color: #ff00ff;
    font-weight: bold;
    font-size: 38px;
    font-family: "Merienda", cursive;
}
.logo-and{
    color: #5D4037;
    font-size: 20px;
}


/*Side-bar start*/

.side-bar {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 300px;
    z-index: 999;
    background-color: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    box-shadow: -10px 0 10px rgba(0, 0, 0, 0.1);
    display: none;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.side-bar.hidden {
    display: none;
}

.side-bar.visible {
    display: flex;
}

.side-bar li {
    width: 100%;
    font-weight: bold;
}

.side-bar a {
    width: 100%;
    font-weight: bold;
}
.open-menu {
    display: none;
}

#logo-span {
    color: #FF69B4;
    font-weight: bold;
    font-size: 32px;
}

#logo-and {
    color: #734060;
    font-weight: bold;
    font-size: 20px;
}

.zero{
    position: relative;
}
.main-zero{
    position: absolute;
    top: -2px;
    right: 20px;
    font-size: 20px;
    color: red;
}
.mobi-zero{
    position: relative;
}
.mobi-main-zero{
    position: absolute;
    top: -2px;
    right: 110px;
    font-size: 20px;
    color: red;

}

@media(max-width :800px){
    .hideOnMobile {
        display: none;
    }

    .open-menu {
        display: block;
    }
}
@media(max-width:500px){
    .side-bar {
        width: 100%;
        top: 0;
        left: 0;
        right: 0;
        height: 250px;
        box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.1);
        transition: all 0.1s ease-out;
    }
    .mobi-zero{
        position: relative;
    }
    .mobi-main-zero{
        position: absolute;
        top: -2px;
        left: 180px;
        font-size: 20px;
        color: red;
    
    }
}