@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Merienda:wght@300..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Sevillana&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playwrite+CU:wght@100..400&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Domine:wght@400..700&display=swap');






* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.about{
    width: 100%;
    background-color: rgb(242, 242, 242);
}
.about-main{
    width: 100%;
    /* height: 100vh; */
    display: flex;
}
.about-main-first{
    width: 45%;
    padding: 0 120px;
        
}
.about-main-first div img{
    height: 480px;

    
}
.main-title h1{
    font-family: "Domine", serif;
    font-size: 42px;
   color: #724060;
}
.about-main-sec{
    width: 53%;
    padding: 30px;
}
.main-title p{
    font-size: 20px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: bold;
    
}
.main-title span{
    font-family: "Dancing Script", cursive;
    font-optical-sizing: auto;
    font-style: normal;
    font-size: 40px;
    color: #999999;
}
.main-title-sub{
    font-size: 18px;
    color: #4d4d4d;
    padding: 20px 0;
    font-weight:500;
 }
 .line-for-about p{
    margin-top: 10px;
    color: #4d4d4d;
    font-size: 17px;
 }
 .philosophy{
    width: 100%;
    background-color: #F0E1D2;
    color: #5E5A41;
    padding: 30px;
    display: flex;
 }
 .first{
    width: 60%;
 }
 .sec{
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
 }
 .sec a{
    margin-left:20px ;
    font-size: 30px;
    color: #724060;
 }
 .sec a:hover{
    color:magenta;
 }
 .sec-contact{
    margin-top: 10px;
 }
 .sec-contact span{
    color: #724060;
 }
 @media(max-width:850px){
    .about-main{
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        
    }
    .about-main-first{
        width: 50%;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        
    }
    .about-main-first img{
        width: auto;
        
    }
    .about-main-sec{
        padding: 0;
    }
    .main-title-sub{
        padding: 0;
    }
    .about-main-sec{
        width: 48%;
        padding: 5px 15px;
    }
 }
 @media(max-width:630px){
    .about-main{
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
      
    }
    .about-main-first{
        width: 100%;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        
    }
    .about-main-first img{
        width: auto;
        
    }
    .about-main-sec{
        padding: 0;
    }
    .main-title-sub{
        padding: 0;
    }
    .about-main-sec{
        width: 100%;
        padding: 5px 15px;
    }
    .philosophy{
        flex-direction: column;
    }
    .first{
        width: 100%;
    }
    .sec{
        width: 100%;
        margin-top: 20px;
    }
 }
 